import React from 'react';
import Navigation from './Navbar/Navigation';
import './header.scss';
import Logo from '../../Images/logo.png'


const Header = () => {
    return (
        <header className="header">
            <div className="logo">
                <img src={Logo} alt="Logo" />
            </div>


            <Navigation />
        </header>

    )
}

export default Header;
