import React from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import './organic.scss';

const Organic = () => {
    return (
        <div className="info-container">
            <section className="info-box text-center">
                <h2 className="text-center">Organic Coffee Beans</h2>
                <p className="p1">We offer a coffee service for offices around the world. We deliver</p>
                <p className="p2">freshly roasted coffee beans every month to your office door. Life</p>
                <p className="p3">is too short to drink bad coffee, especially at work, where you</p>
                <p className="p4">should just focus on work and not worry about your next coffee break.</p>
                <p className="p5">We have some delicious options for you!</p>
                <NavLink to="/shopsection"> <button className="btn">Buy</button></NavLink>


            </section>

        </div>
    )
}

export default withRouter(Organic);
