import React, { useContext } from 'react';
import './cart.scss';


import {
    Alert,
    Form,
    Button,
    Row,
    Col,
    Table,
} from 'react-bootstrap';

import { Cart } from '../../App';

const ShoppingCart = () => {
    const { cart, setCart } = useContext(Cart);

    const changeQuantity = (tempProd, quantityIn) => {
        const product = { ...tempProd };
        if (quantityIn === '0') return removeItem(product);
        product.quantity = parseInt(quantityIn, 10);
        const prodIndex = cart.map((item) => item.key).indexOf(product.key);
        const tempCart = [...cart];
        tempCart[prodIndex] = product;

        setCart(tempCart);
        return null;
    };

    console.log(cart.length);

    const removeItem = (product) => {
        const prodIndex = cart.map((item) => item.key).indexOf(product.key);
        const tempCart = [...cart];
        setCart(tempCart.slice(0, prodIndex).concat(tempCart.slice(prodIndex + 1)));
    };

    return (


        <div id="shopping-cart">
            <h1 className="text-center">Your Basket</h1>
            {cart.map((product) => (
                <CartItem product={product} key={`cart-${product.key}`} changeQuantity={changeQuantity} />
                
            ))}
            {cart.length > 0 && <Totals cart={cart} />}
            <span>{cart.length}</span>
        </div>
       
    );
};



const Totals = ({ cart }) => {
    const subTotal = cart.map((item) => item.price * item.quantity).reduce((total, item) => total + item);
    const taxRate = 70;

    return (
        <Table striped bordered hover variant="dark">
            <tbody>
                <tr>
                    <td>Sub Total: </td>
                    <td>{subTotal.toFixed(2)} DKK</td>
                </tr>
                <tr>
                    <td>Delivery: </td>
                    <td>{parseFloat(taxRate).toFixed(2)} DKK</td>
                </tr>
                <tr>
                    <td>Total: </td>
                    <td>{parseFloat(subTotal + (taxRate)).toFixed(2)} DKK</td>
                </tr>
            </tbody>
        </Table>
    );
};

const CartItem = ({ product, changeQuantity }) => (
    <Alert variant="info">
        <div className="cart-item">
            <img src={product.image} alt={product.name} align="left" onMouseOver={e => (e.currentTarget.src = product.backImage)} onMouseLeave={e => (e.currentTarget.src = product.image)} />
            <p>{product.name}</p>
            <p>Cost: {parseFloat(product.quantity * product.price).toFixed(0)}</p>
        </div>
        <Form>
            <Form.Group as={Row}>
                <Form.Label column sm="2">
                    Quantity
        </Form.Label>
                <Col sm={10}>
                    <Form.Control
                        sm="10"
                        as="select"
                        value={product.quantity}
                        onChange={(event) => changeQuantity(product, event.target.value)}
                    >
                        <option>0</option>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                        <option>6</option>
                    </Form.Control>
                </Col>
            </Form.Group>
        </Form>
        <Button size="sm" onClick={() => changeQuantity(product, '0')} variant="warning">Remove</Button>




    </Alert>


 
);

export default ShoppingCart;
