import React from 'react';
import '../Contact/contact.scss';


const Contact = () => {

    const handleSubmit = () => {

        alert('Your message has been send, and we will reply to you as soon as possible');

    }



    return (
        <div className="container">

<h1 id="contact" className="h2-responsive font-weight-bold text-center my-4">CONTACT</h1>

    <form id="contact-form" onSubmit={handleSubmit}>
    <div className="form-group">
        <label htmlFor="name">Name</label>
        <input type="text" className="form-control" required />
    </div>
    <div className="form-group">
        <label htmlFor="exampleInputEmail1">Email address</label>
        <input type="email" className="form-control" aria-describedby="emailHelp" required />
    </div>

    <div className="form-group">
        <label htmlFor="text">Company</label>
        <input type="text" className="form-control" aria-describedby="company" required />
    </div>

    <div className="form-group">
        <label htmlFor="message">Message</label>
        <textarea className="form-control" rows="5" required></textarea>
    </div>
    <button type="submit" className="btn">Submit</button>
</form>

        </div>
    )
}

export default Contact
