import React from 'react';
import './information.scss';

const Information = () => {
    return (
        <section className="information">
            <div className="content">
                <section className="boxes">
                    <div className="box 1">
                        <i className="fas fa-seedling fa-4x"></i>
                        <h3>Organic Beans</h3>
                        <p>We only use organic and high quality coffee beans, no compromising on the taste</p>
                    </div>

                    <div className="box 2">
                        <i className="fas fa-dollar-sign fa-4x"></i>
                        <h3>Price matches quality</h3>
                        <p>We are not the cheapest, but our prices matches the quality of our coffee beans</p>
                    </div>

                    <div className="box 3">
                        <i className="fas fa-dolly fa-4x"></i>
                        <h3>Cheap shipping</h3>
                        <p>Cheap and fast shipping world wide, so you can start your new coffee adventure soon</p>
                    </div>
                </section>

            </div>



        </section>
    )
}

export default Information;
