import prodOne from '../../assets/img/p1.jpg';
import prodTwo from '../../assets/img/p2.jpg';
import prodThree from '../../assets/img/p3.jpg';
import prodFour from '../../assets/img/p4.jpg';
import prodFive from '../../assets/img/p5.jpg';
import prodSix from '../../assets/img/p6.jpg';
import prodSeven from '../../assets/img/p7.jpg';
import prodEight from '../../assets/img/p8.jpg';
